body {
  margin: 0;
}

h1,
p {
  font-family: Lato;
}

.container {
  width: 100vw;
  height: 100vh;
  background: #e5ddd5;
  margin: 0;
  padding: 0;
}

.header-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: fixed;
  left: 0px;

  width: 100vw;
  height: 60px;

  background: #ededed;
}

.header {
  top: 0px;
}

.footer {
  bottom: 0px;
}

.header-content {
  display: flex;
  margin: 0px 5px;
  font-size: 25px;
}

.chat-btn {
  color: black;
  background: #b4ec88;
  border: 1px solid #b4ec88;
  border-radius: 25px;
  padding: 5px 25px;
  margin: 0px 5px;
  cursor: pointer;
  height: 45px;
  font-size: 15px;
}

.chat-btn:active {
  background: #7aa855;
}

.chat-icon {
  margin: 0px 5px;
  min-width: 40px;
  max-width: 40px;
  height: 40px;
  border: 1px solid black;
  border-radius: 50%;
  padding: 5px 10px;
}

.chat-input {
  width: 100%;
  padding: 5px 10px;
  height: 32px;
  font-size: 15px;
  border: 1px solid black;
  border-radius: 25px;
}

@media only screen and (max-width: 520px) {
  .header-content {
    font-size: 15px;
  }
  .chat-btn {
    padding: 5px;
  }
}

/* ScrollBar */
::-webkit-scrollbar {
  width: 5px;
  cursor: pointer;
}

::-webkit-scrollbar-track {
  background: #1c1c1c;
}

::-webkit-scrollbar-thumb {
  background: #49ba8e;
}
